}
.picture-4 {
  grid-area: 2 / 5 / 4 / 6;
}
.picture-5 {
  grid-area: 2 / 6 / 4 / 7;
  margin-top: -15px;
}
.picture-6 {
  grid-area: 3 / 7 / 5 / 8;
}
.picture-7 {
  grid-area: 4 / 2 / 6 / 3;
  margin-top: -6px;
}
.picture-8 {
  grid-area: 5 / 3 / 7 / 4;
}
.picture-9 {
  grid-area: 4 / 5 / 6 / 6;
}
.picture-10 {
  grid-area: 4 / 6 / 6 / 7;
  margin-top: -15px;
}

.picture-set.element-animation.element-show {
  animation-name: linescale, opacity;
  animation-duration: 1000ms;
}

@keyframes linescale {
  0% {
    transform: scaleX(0%) translate(-50%, -50%) rotateX(270deg) rotateY(0deg);
  }
  100% {
    transform: scaleX(100%);
  }
}

@keyframes opacity {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}
